import errorMessages from "./errorMessages";
import { AsYouType, validatePhoneNumberLength, CountryCode } from 'libphonenumber-js';
import { getCountryPropByKey } from './countriesList'
import { iFormField } from "../models/form-field.model";

export const required = (type, value: any, lang: "ar" | "en" = 'en') => {
    if(type === 'phone') {
        const [_, mobileNumber] = value.split('-');
        return mobileNumber?.length && mobileNumber.trim().length ? null : errorMessages.required[lang]();
    }

    if(type === 'file') {
        return value?.value ? null : errorMessages.required[lang]();
    }

    if(typeof value === "number") {
        return value !== null && value !== undefined ? null : errorMessages.required[lang]();
    }
    
    return value?.length && value.trim().length ? null : errorMessages.required[lang]();
};

export const min = (min: number) => (_, value: number, lang: "ar" | "en" = 'en') => {
    if (!value && value !== 0) return null;
    return Number(value) >= min ? null : errorMessages.min[lang](min);
}

export const max = (max: number) => (_, value: number, lang: "ar" | "en" = 'en') => {
    if (!value && value !== 0) return null;
    return Number(value) <= max ? null : errorMessages.max[lang](max);
}

export const minLength = (min: number) => (_, value: string, lang: "ar" | "en" = 'en') => {
    return value?.length >= min ? null : errorMessages.minLength[lang](min);
};

export const maxLength = (max: number) => (_, value: string, lang: "ar" | "en" = 'en') => {
    return value?.length <= max ? null : errorMessages.maxLength[lang](max);
};

export const maxSize = (maxSizeMB: number) => (_, field: iFormField, lang: "ar" | "en" = 'en') => {
    const maxSizeBytes = maxSizeMB * 1024 * 1024;
    return field?.value?.size <= maxSizeBytes ? null : errorMessages.maxSize[lang](maxSizeMB);
}

export const isEmail = (_, value: string, lang: "ar" | "en" = 'en') => {
    if(!value || !value.trim().length) return errorMessages.isEmail[lang]();

    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return emailRegex.test(value) ? null : errorMessages.isEmail[lang]();
};

export const isPhoneNumber = (_, value: string, lang: "ar" | "en" = 'en') => {
    if(!value || !value.trim().length) return errorMessages.isPhoneNumber[lang]();

    const [countryCode, mobileNumber] = value.split('-');

    const number = mobileNumber?.startsWith('0')
        ? mobileNumber.slice(1)
        : mobileNumber;

    const phoneRegex = /^\+?[0-9]{7,15}$/;
    if (!phoneRegex.test(mobileNumber)) {
        return errorMessages.isPhoneNumber[lang]();
    }

    const countryAlpha2 = getCountryPropByKey('country_code', countryCode, 'alpha_2_code');

    const phoneNumber = new AsYouType(countryAlpha2 as CountryCode).input(countryCode + number);
    const lengthValidation = validatePhoneNumberLength(phoneNumber);

    if (lengthValidation) {
        return errorMessages.isPhoneNumber[lang]();
    }

    return null;
};

export const pattern = (pattern: string) => (_, value: string, lang: "ar" | "en" = 'en') => {
    if(!value || !value.trim().length) return errorMessages.pattern[lang]();

    const cleanedPattern = pattern.replace(/^\/|\/$/g, "");
    const regExp = new RegExp(cleanedPattern);
    return regExp.test(value) ? null : errorMessages.pattern[lang]();
}

export const isAlphabetic = (_, value: string, lang: "ar" | "en" = 'en') => {
    if(!value || !value.trim().length) return errorMessages.isAlphabetic[lang]();

    const alphaRegex = /^[a-zA-Z\s'-]+$/;
    return alphaRegex.test(value) ? null : errorMessages.isAlphabetic[lang]();
};

export const isNumeric = (_, value: string, lang: "ar" | "en" = 'en') => {
    if(!value || !value.trim().length) return errorMessages.isNumeric[lang]();

    const numericRegex = /^[0-9]+$/;
    return numericRegex.test(value) ? null : errorMessages.isNumeric[lang]();
}

export const asyncValidate = (url: string, message: string) => async (_, value: string) => {
    try {
        const response = await fetch(url, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({ value }),
        });

        const data = await response.json();
        return data.valid ? null : message;
    } catch (error) {
        console.error('Async validation error:', error);
        return 'Validation service is unavailable. Please try again later.';
    }
}

export const mustBeTruthy = (_, value: boolean) => {
    return value === true ? null : 'This field must be checked.';
}