import { Component, Method, Prop, State, h, Element } from '@stencil/core';
import { iFormField } from './models/form-field.model';
import * as _v from './utils/formValidators';

export interface iComponentSettings {
  language: string;
  formId: string;
  formFields?: iFormField[];
  formValues?: { [key: string]: any };
  actionUrl?: string;
}

@Component({
  tag: 'yaseer-form',
  styleUrl: 'yaseer-form.css',
  shadow: true,
})
export class YaseerForm {
  @Element() el: HTMLElement;
  @Prop() componentSettings: string | iComponentSettings;

  @State() formFields: iFormField[];
  @State() settings: iComponentSettings;
  @State() isSubmitting: boolean = false;

  async connectedCallback() {
    if (typeof this.componentSettings === 'string') {
      this.settings = JSON.parse(this.componentSettings);
    } else {
      this.settings = { ...this.componentSettings, formFields: this.componentSettings?.formFields || [] };
    }

    const formJSON = this.settings.formFields;
    if (!formJSON || !formJSON.length) return;

    this.formFields = this.formatJSONData(formJSON);
  }

  formatJSONData(data: any[]): iFormField[] {
    return data.map(item => {
      const fieldObject: iFormField = {
        ...item,
        validators: this.formatValidators(item.validators || []),
        errors: [],
        isPristine: true,
      };

      if (this.settings.formValues && this.settings.formValues.hasOwnProperty(item.key)) {
        fieldObject.value = this.settings.formValues[item.key];
      }

      return fieldObject;
    });
  }

  formatValidators(validators: { [key: string]: boolean | string }[]): Function[] {
    const formattedValidators: Function[] = [];

    for (let i = 0; i < validators.length; i++) {
      const validatorKey = Object.keys(validators[i])[0];
      const validatorValue = validators[i][validatorKey];

      if (typeof validatorValue !== 'boolean') {
        formattedValidators.push((_v as any)[validatorKey](validatorValue));
      } else {
        formattedValidators.push((_v as any)[validatorKey]);
      }
    }

    return formattedValidators;
  }

  @Method()
  async getFormValues(): Promise<{ [key: string]: any }> {
    const formBuilderElement = this.el.shadowRoot.querySelector('form-builder');

    if (formBuilderElement) {
      return await formBuilderElement.getFormValues();
    }
  }
  @Method()
  async toggleSpinner(show: boolean) {
    this.isSubmitting = show;
  }

  @Method()
  async submitForm() {
    const formBuilderElement = this.el.shadowRoot.querySelector('form-builder');

    if (formBuilderElement) {
      try {
        const payload = await formBuilderElement.handleSubmit();
        return { success: true, data: payload };
      }
      catch (error) {
        return { success: false, error };
      }
    }
  }

  // componentDidRender() {
  //   this.generateWatermark();
  // }

  // private generateWatermark() {
  //   this.el.shadowRoot.appendChild(document.createElement('yaseer-watermark'));
  // }

  render() {
    if (!this.formFields) {
      return <div>Loading form...</div>;
    }

    if (!this.formFields.length) {
      return <div>No form fields available.</div>;
    }

    return (
      <div style={{ position: 'relative' }}>
        <form-builder
          formFields={this.formFields}
          language={this.settings?.language}
          formId={this.settings?.formId}
          actionUrl={this.settings?.actionUrl}>
          <slot></slot>
        </form-builder>

        <div class={`form-spinner ${this.isSubmitting ? 'visible' : ''}`}>
          <slot name="spinner">
            loading...
          </slot>
        </div>
      </div>
    );
  }
}