import { Component, State, h, Listen, Prop, Method, Event } from '@stencil/core';
import fieldMapper from '../../utils/fieldMapper';
import evaluateRule from '../../utils/fieldEvaluator';
import { eFormField, iFormField } from '../../models/form-field.model';

@Component({
    tag: 'form-builder',
    styleUrl: 'form-builder.component.css',
    shadow: false,
    scoped: false,
})
export class FormBuilderComponent {
    @Prop() formId?: string;
    @Prop() formFields: iFormField[];
    @Prop() language: string = 'en';
    @Prop() actionUrl?: string;

    @State() sortedFields: Map<string, iFormField>;

    @Event({ bubbles: true, composed: true }) formUpdated: any;

    connectedCallback() {
        if (!this.formFields) return;

        this.sortedFields = new Map(this.formFields.sort((a, b) => a.sequence - b.sequence).map(f => [f.key, f]));

        for (let field of this.sortedFields.values()) {
            field.errors = this.validateField(field);
        }

        this.evaluateAllRules();
    }

    @Listen('fieldBlurred')
    handleFieldBlurred(event: CustomEvent<{ key: string }>) {
        const field = this.sortedFields.get(event.detail.key);
        if (!field) return;

        Object.assign(field, {
            isPristine: false,
        });

        field.errors = this.validateField(field);
        this.evaluateAllRules();

        this.sortedFields = new Map(this.sortedFields);
    }

    @Listen('valueChanged')
    handleValueChange(event: CustomEvent<{ key: string; value: any }>) {
        const field = this.sortedFields.get(event.detail.key);
        if (!field) return;

        Object.assign(field, {
            value: event.detail.value,
        });

        field.errors = this.validateField(field);
        this.evaluateAllRules();

        this.sortedFields = new Map(this.sortedFields);

        this.formUpdated.emit({
            isValid: this.isFormValid(),
            formFields: [...this.formFields.values()]
                .filter(field => !field.hidden && !field.disabled && field.fieldType !== eFormField.legend)
                .reduce((obj, field) => {
                    obj[field.key] = {
                        value: field.value,
                        errors: field.errors,
                    };
                    return obj;
                }, {} as { [key: string]: { value: any; errors: string[] } }),
        });
    }

    private validateField(field: iFormField): string[] {
        const value = field.fieldType == eFormField.file || field.fieldType == eFormField.multifile
            ? field
            : field.value;

        return field.validators.map(validator => validator(field.fieldType, value, this.language || 'en')).filter(error => error) as string[];
    }

    private evaluateAllRules() {
        this.sortedFields.forEach(field => {
            let checkEnable = field.shouldEnable && field.fieldType !== eFormField.legend;
            let checkDisplay = field.shouldDisplay && field.fieldType !== eFormField.legend;

            // if (!field.shouldEnable || field.fieldType === eFormField.legend) return;

            if (checkEnable) {
                const shouldEnable = evaluateRule(field.shouldEnable, this.sortedFields);
                field.disabled = !shouldEnable;

                // Optional: clear value when disabled
                if (!shouldEnable) {
                    // field.value = null;
                    field.errors = [];
                    field.isPristine = true;
                }
            }

            if (checkDisplay) {
                const shouldDisplay = evaluateRule(field.shouldDisplay, this.sortedFields);
                field.hidden = !shouldDisplay;

                // Optional: clear value when disabled
                if (!shouldDisplay) {
                    field.value = null;
                    field.errors = [];
                    field.isPristine = true;
                }
            }
        });

        // Trigger rerender
        this.sortedFields = new Map(this.sortedFields);
    }

    componentWillRender() {
        if (!this.sortedFields) return;
        for (const field of this.sortedFields?.values()) {
            field.errors = this.validateField(field);
        }
    }

    isFormValid(): boolean {
        if (!this.sortedFields) return false;
        let isValid = true;

        for (const field of this.sortedFields.values()) {

            if (field.hidden || field.disabled) continue;

            if (field.errors.length) {
                isValid = false;
                break;
            }
        }

        return isValid;
    }

    markAllFieldsTouched() {
        if (!this.sortedFields) return;

        this.sortedFields.forEach(field => {
            field.isPristine = false;
            field.errors = (field.hidden || field.disabled) ? [] : this.validateField(field);
        });

        this.sortedFields = new Map(this.sortedFields);
    }

    @Method()
    getFormValues(): Promise<{ [key: string]: any }> {
        return new Promise((resolve) => {
            const payload: { [key: string]: any } = {};

            if (!this.sortedFields) return payload;

            for (const field of this.sortedFields.values()) {
                if (field.fieldType === eFormField.legend) continue;

                payload[field.key] = field.value;
            }

            resolve(payload);
        });
    }

    @Method()
    handleSubmit(): Promise<{ [key: string]: any }> {
        return new Promise((resolve, reject) => {
            this.evaluateAllRules();
            if (!this.isFormValid()) {
                this.markAllFieldsTouched();

                reject({
                    message: 'Form validation failed.',
                    formFields: [...this.formFields.values()]
                        .filter(field => !field.hidden && !field.disabled && field.fieldType !== eFormField.legend)
                        .reduce((obj, field) => {
                            obj[field.key] = {
                                value: field.value,
                                errors: field.errors,
                            };
                            return obj;
                        }, {} as { [key: string]: { value: any; errors: string[] } }),

                });
                return;
            }

            const payload: { [key: string]: any } = [...this.sortedFields.values()]
                .filter(field => field.fieldType !== eFormField.legend)
                .reduce((object, field) => {
                    object[field.key] = field.value;
                    return object;
                }, {});

            if (this.actionUrl?.length) {
                // If actionUrl is provided, submit the form data to the URL
                const formElement = document.createElement('form');
                formElement.method = 'POST';
                formElement.action = this.actionUrl;
                formElement.style.display = 'none';

                for (const key in payload) {
                    const inputElement = document.createElement('input');
                    inputElement.type = 'hidden';
                    inputElement.name = key;
                    inputElement.value = payload[key];
                    formElement.appendChild(inputElement);
                }

                document.body.appendChild(formElement);
                formElement.submit();
                document.body.removeChild(formElement);
            }
            else {
                resolve(payload);
            }


        });
    }

    render() {
        if (!this.sortedFields) { return null; }

        const formContent = (
            <div class="form-fields-grid">
                {[...this.sortedFields.values()].map((field, index) => {
                    const error = field.errors.length && !field.isPristine && !field.hidden && !field.disabled ? (
                        <span class="field-error" style={{ order: '3' }}>
                            {field.errors[0]}
                        </span>
                    ) : null;

                    const attributes = {
                        language: this.language || 'en',
                        field,
                        className: 'form-field-container', //`${field.key}-form-field`,
                        part: `${field.key}-form-field`,
                        isInvalid: field.errors.length && !field.isPristine,
                        isDisabled: field.disabled ?? false,
                        isHidden: field.hidden ?? false,
                        value: field.value,
                    };

                    const checkableTypes = ['toggle', 'checkbox'];

                    if (checkableTypes.includes(field.fieldType)) {
                        Object.assign(attributes, { isChecked: field.value == true });
                    }

                    if (field.fieldType === eFormField.radio) {
                        Object.assign(attributes, { value: field.value });
                    }

                    return (
                        <div class={`field-col-span-${field.size}`}
                            key={index}>
                            {h(
                                fieldMapper.get(field.fieldType),
                                { ...attributes },
                                error
                            )}
                        </div>
                    )
                })}

                <div class="field-col-span-4">
                    <slot></slot>
                </div>
            </div>
        );

        return (
            <form class="wd-custom-form" id={this.formId || ''}>
                {formContent}
            </form>
        );
    }
}

